<template>
    <div class="container">
        <div class="row header">
            <h1>Лидерборды - супер!</h1>
            <button class="btn btn-primary" @click="create">Создать лидерборд</button>
        </div>
        <p class="text-center" v-if="loading">Загрузка...</p>
        <p class="alert alert-danger" v-if="error">{{error}}</p>
        <div class="row">
            <div class="col-md-6 cell" v-for="board in items" :key="board.id">
                <div class="card">
                    <div class="card-body">
                        <h5>{{board.name}}</h5>
                        <p class="text-muted" v-if="board.subscriptionActive">Оплачен до {{board.subscriptionTo}}</p>
                        <p class="text-danger" v-if="!board.subscriptionActive"><strong>Не оплачен</strong></p>
                        <div class="btn-line">
                            <button type="button" class="btn btn-primary" @click="configure(board.id)">Настроить</button>
                            <button type="button" class="btn btn-primary" @click="pay(board.id)">Оплатить</button>
                        </div>
                        <div class="btn-line">
                            <button type="button" class="btn btn-primary" @click="remove(board.id)">Удалить</button>
                            <button type="button" class="btn btn-primary" @click="open(board.id)">Открыть</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <Pagination :cur-page="page" :num-pages="numPages" :handler="loadPage" :total="total"></Pagination>
        <p class="text-center m-0">
            <a href="javascript:void(0)" @click="logout"><small>Выход из приложения</small></a>
        </p>
    </div>
</template>

<script>
    import boardAPI from "@/services/BoardService";
    import ApiCodes from "@/services/ApiCodes";
    import Pagination from "@/components/util/Pagination";
    import {HandleApiResponse} from "@/components/util/mixins";
    import loginApi from "@/services/LoginService";

    const PAGE_SIZE = 6;

    export default {
        name: "Boards",
        components: {Pagination},
        mixins: [HandleApiResponse],
        data() {
            return {
                items: [],
                loading: true,
                error: false,
                page: 0,
                numPages: 0,
                total: 0
            }
        },
        created() {
            this.loadPage(0);
        },
        methods: {
            loadPage(page) {
                this.loading = true;
                this.error = null;
                this.handleResponse(() => boardAPI.getBoards(page))
                    .then(result => {
                        this.items = result.items;
                        this.page = result.page
                        this.total = result.total;
                        this.numPages = Math.ceil(result.total / PAGE_SIZE);
                    });
            },
            configure(id) {
                this.$router.push(`/cp/boards/${id}/config`);
            },
            open(id) {
                this.$router.push(`/cp/boards/${id}`);
            },
            pay(id) {
                this.handleResponse(() => boardAPI.getPaymentLink(id))
                    .then(({link}) => {
                        if(link) {
                            open(link, null, 'status=no,menubar=no,toolbar=no,location=no,noopener');
                        }
                    })
            },
            remove(id) {
                this.handleResponse(() => boardAPI.deleteBoard(id))
                    .then(() => this.loadPage(this.page));
            },
            create() {
                this.$router.push('/cp/boards/new/config');
            },
            logout() {
                loginApi.logout();
                this.$router.push('/');
            }
        }
    }
</script>

<style scoped>
    .container {
        max-width: 800px;
        flex-grow: 1;
        margin: 10px 0 10px;
        padding: 50px;
        background-color: white;
        align-self: center;
    }
    .header {
        margin-bottom: 50px;
        padding: 0 5px;
    }
    .header button {
        margin-left: auto;
    }
    .btn-line {
        display: flex;
        justify-content: space-evenly;
        margin: 0 -10px 15px;
    }
    .btn-line button {
        flex-grow: 1;
        margin: 0 10px;
    }

    .cell {
        padding: 5px;
    }

    .btn {
        text-transform: uppercase;
        font-size: 0.8em;
    }

</style>