import ApiCodes from "@/services/ApiCodes";
import mock from "@/services/mock";

const TOKEN_KEY = 'token';

let state = {
    logged: false,
    token: null
};

function logout() {
    localStorage.removeItem(TOKEN_KEY);
}

function isLogged() {
    return state.logged;
}

async function tryLogin() {
    if(state.logged) return true;
    if(localStorage.getItem(TOKEN_KEY)) {
        await mock.delay(1000);
        return true;
    } else {
        return false;
    }
}

async function logIn(login, pass) {
    if(pass !== '1') throw new Error(ApiCodes.PASSWORD_INVALID);
    await mock.delay(1000);
    localStorage.setItem(TOKEN_KEY, 'OK');
    state = {
        logged: true,
        token: '111',
        login,
        isAdmin: login === 'admin'
    }
    return state;
}


const loginApi = {isLogged, logIn, tryLogin, logout};


export default loginApi;