<template>
    <div class="root" :style="{backgroundColor: style.background || '#C4D3DC'}">
        <div class="banner" :style="{backgroundImage: style.banner && `url(${style.banner})`}">
            <p v-if="style.title" class="title">{{style.title}}</p>
            <div class="banner-btn">
                <button class="btn btn-primary" @click="openCoverLoad = true">Загрузить обложку</button>
                <button class="btn btn-primary mt-1" @click="openTitleEdit = true">Изменить текст</button>
            </div>

        </div>
        <div class="content">
            <div class="form-row">
                <div class="col-md-4">
                    <input type="text" class="form-control mb-2" placeholder="Найти аккаунт" name="query" @keyup.enter="filter">
                </div>
                <div class="col-md-8">
                    <button class="btn btn-primary float-right ml-3 mt-1" @click="openPublic">Открыть публично</button>
                    <button class="btn btn-primary float-right mt-1" @click="openPromptColor = true;colorError = false">Изменить цвет фона</button>
                </div>
            </div>
            <p v-if="loading" class="text-center m-2">Загрузка...</p>
            <div class="table-responsive mt-4">
                <table class="table items-tbl">
                    <thead>
                        <tr>
                            <th>#</th>
                            <th>аккаунт</th>
                            <th>коментарии</th>
                            <th>лайки</th>
                            <th>прочие действия</th>
                            <th>баллы вручную</th>
                            <th>всего баллов</th>
                            <th>добавить баллы</th>
                            <th>снять баллы</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr :key="item.pos" v-for="(item,i) of list">
                            <th>{{item.pos}}</th>
                            <td>{{item.login}}</td>
                            <td>{{item.commentPoints}}</td>
                            <td>{{item.likePoints}}</td>
                            <td>{{item.otherPoints}}</td>
                            <td>{{item.manualPoints}}</td>
                            <td>{{item.total}}</td>
                            <td><a href="javascript:void(0)" @click="openAddBonus = i + 1">Добавить</a></td>
                            <td><a href="javascript:void(0)" @click="openSubBonus = i + 1">Снять</a></td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <Pagination :total="total" :num-pages="numPages" :cur-page="curPage" :handler="open"></Pagination>
        </div>

        <div class="modal-backdrop fade show" v-if="openCoverLoad || openSubBonus || openAddBonus || openTitleEdit || openPromptColor"></div>

        <Modal v-if="openTitleEdit" title="Изменить заголовок" btn-text="Изменить" :on-close="closeModal" :on-change="updateTitle">
            <textarea class="form-control mr-3" ref="titleField" cols="40" rows="2">
                {{style.title}}
            </textarea>
        </Modal>

        <Modal v-if="openAddBonus" title="Добавить бонус" btn-text="Добавить" :on-close="closeModal" :on-change="() => changeBonus(openAddBonus, 1 * $refs.bonus.value)">
            <input type="text" class="form-control mr-3" :value="0" ref="bonus"/>
        </Modal>

        <Modal v-if="openSubBonus" title="Снять бонус" btn-text="Снять" :on-close="closeModal" :on-change="() => changeBonus(openAddBonus, -1 * $refs.bonus.value)">
            <input type="text" class="form-control mr-3" :value="0" ref="bonus"/>
        </Modal>

        <Modal v-if="openCoverLoad" title="Загрузить обложку" btn-text="Загрузить" :on-close="closeModal" :on-change="uploadCover">
            <div class="form-group mx-sm-4 mb-2">
                <input type="file" ref="cover" class="form-control-file" placeholder="Файл обложки">
            </div>
        </Modal>

        <Modal v-if="openPromptColor" btn-text="Изменить" title="Изменить цвет фона" :on-close="closeModal" :on-change="updateColor">
            <div class="input-group mr-2">
                <div class="input-group-prepend">
                    <span class="input-group-text">#</span>
                </div>
                <input type="text" ref="color" class="form-control" :class="{'is-invalid' : colorError}">
            </div>
        </Modal>

    </div>
</template>

<script>
    import {HandleApiResponse} from "@/components/util/mixins";
    import boardApi from "@/services/BoardService";
    import Pagination from "@/components/util/Pagination";
    import Modal from "@/components/board/Modal";

    const PAGE_SIZE = 10;

    export default {
        name: "Board",
        components: {Modal, Pagination},
        mixins: [HandleApiResponse],
        data() {
            return {
                style: {},
                list: [],
                loading: true,
                error: null,
                id: null,
                total: 0,
                numPages: 0,
                curPage: 0,
                query: null,
                openCoverLoad: false,
                openPromptColor: false,
                openAddBonus: 0,
                openSubBonus: 0,
                openTitleEdit: false,
                colorError: false
            }
        },
        created() {
            this.id = parseInt(this.$route.params.id);
            this.handleResponse(() => boardApi.getBoardStyle(this.id))
                .then(data => {
                    if(data) {
                        this.style = data;
                    }
                });
            this.open(0);
        },
        methods: {
            open(page) {
                this.handleResponse(() => boardApi.getBoardStats(this.id, page, this.query || ''))
                    .then(data => {
                        if(data) {
                            this.list = data.users;
                            this.curPage = data.page;
                            this.numPages = Math.ceil(data.total / PAGE_SIZE);
                            this.total = data.total;
                        }
                    });
                return false;
            },
            filter(e) {
                this.query = e.target.value;
                this.open(0);
            },
            uploadCover() {
                const file = this.$refs.cover.files[0];
                if(file) {
                    this.updateStyle({file});
                }
            },
            updateTitle() {
                this.updateStyle({title: this.$refs.titleField.value});
            },
            changeBonus(pos, delta) {
                if(delta) {
                    this.closeModal();
                    this.handleResponse(() => boardApi.changeBonus(this.id, this.list[pos].login, delta))
                        .then(r => {
                            if (r) {
                                this.open(this.curPage);
                            }
                        });
                }
            },
            closeModal() {
                this.openCoverLoad = false;
                this.openPromptColor = false;
                this.openTitleEdit = false;
                this.openAddBonus = 0;
                this.openSubBonus = 0;
            },
            updateColor() {
                const background = this.$refs.color.value;
                if(/[0-9A-F]{6}/i.test('' + background)) {
                    this.updateStyle({background : '#' + background});
                } else {
                    this.colorError = true;
                }
            },
            updateStyle(style) {
                this.handleResponse(() => boardApi.storeBoardStyle(this.id, style))
                    .then(data => {
                        this.closeModal();
                        if (data) {
                            this.style = data;
                        }
                    });
            },
            openPublic() {
                if(this.style.url) {
                    location.href = this.style.url;
                }
            }
        }
    }
</script>

<style scoped>
    .root {
        display: flex;
        flex-direction: column;
    }
    .title {
        white-space: pre;
        font-size: 9vmin;
        color: white;
        text-transform: uppercase;
        font-weight: bold;
        line-height: 1.2;
        max-width: 100%;
    }
    .banner {
        overflow: hidden;
        width: 100%;
        height: 25vh;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .content {
        width: 100%;
        margin: 0 auto 20px;
        max-width: 1200px;
        padding: 30px 50px 0;
        align-self: center;
        flex-grow: 1;
        background: #FAFAFA;
        box-shadow: 0 2px 4px 2px rgba(0, 0, 0, 0.25);
    }
    .items-tbl tbody td:nth-child(n+3) {
        text-align: center;
    }
    .items-tbl tbody td:nth-child(2) {
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: 200px;
    }
    .items-tbl thead th {
        text-align: center;
    }
    .banner-btn {
        position: absolute;
        right: 50px;
        top: 50px;
        display: flex;
        flex-direction: column;
        z-index: 1;
    }
</style>