<template>
    <div class="content">
        <div class="card login-card">
            <h5 class="card-header text-center">
                Вход в приложение
            </h5>
            <div class="card-body">
                <div class="form-group">
                    <label for="login">Логин</label>
                    <input v-model="login" type="text" id="login" :class="{'is-invalid': errors.login}" class="form-control" autocomplete="off"/>
                    <small v-if="errors.login" class="invalid-feedback">{{errors.login}}</small>
                </div>
                <div class="form-group">
                    <label for="pass">Пароль</label>
                    <input v-model="pass" type="password" id="pass" class="form-control" :class="{'is-invalid': errors.pass}">
                    <small v-if="errors.pass" class="invalid-feedback">{{errors.pass}}</small>
                </div>
                <div class="col-auto text-center">
                    <button @click="doLogin" type="button" class="btn btn-primary" :disabled="!login.length || !pass.length">Войти</button>
                </div>
            </div>
            <div class="card-footer footer">
                <router-link to="/"><small>Зарегистрироваться</small></router-link>
                <router-link to="/"><small>Восстановить доступ</small></router-link>
            </div>
        </div>
    </div>

</template>

<script>
    import loginApi from "@/services/LoginService";
    import ApiCodes from "@/services/ApiCodes";

    export default {
        name: "Login",
        data() {
            return {
                errors: {},
                login: '',
                pass: '1',
                loading: false
            }
        },
        methods: {
            doLogin() {
                this.errors = {};
                this.loading = true;
                loginApi.logIn(this.login, this.pass).then((user) => {
                    if(user.isAdmin) {
                        this.$router.push("/admin/users");
                    } else {
                        this.$router.push("/cp/boards");
                    }
                }).catch(err => {
                   this.loading = false;
                   switch (err.message) {
                        case ApiCodes.PASSWORD_INVALID: this.errors = {pass: 'Некорректный пароль'}; break;
                        case ApiCodes.LOGIN_INVALID: this.errors = {login: 'Некорректный логин'}; break;
                        default: this.errors = {internal: 'Произошла ошибка. Повторите попытку позже'}
                   }
                });
            }
        }
    }
</script>

<style scoped>
    .content {
        flex-grow: 1;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .login-card {
        max-width: 400px;
        width: 100%;
    }

    .footer {
        display: flex;
        justify-content: space-evenly;
    }
</style>