<template>
    <div class="content">
        <h2>Настройте лидерборд</h2>

        <p class="text-center mt-3" v-if="loading">Загрузка...</p>

        <p class="alert alert-danger" v-if="error">{{error}}</p>

        <h5>Страница</h5>

        <BoardConfigField v-for="f of fields.page" :key="f.id" :binding="$v.item[f.id]" :code="f.id" :title="f.title"></BoardConfigField>

        <h5>Действия пользователя</h5>

        <BoardConfigField v-for="f of fields.actions" :key="f.id" :binding="$v.item[f.id]" :code="f.id" :title="f.title"></BoardConfigField>

        <div class="form-group row">
            <div class="col-sm-8">Учитывать только подписчиков </div>
            <div class="col-sm-4">
                <VueToggles v-model="item.onlySubscribers" @click="item.onlySubscribers = !item.onlySubscribers" width="70"  height="35" checked-text="Да" unchecked-text="Нет"></VueToggles>
            </div>
        </div>

        <h5>Обратная связь</h5>

        <BoardConfigField v-for="f of fields.feedback" :key="f.id" :binding="$v.item[f.id]" :code="f.id" :title="f.title"></BoardConfigField>

        <h5>Прочее</h5>

        <div class="form-group row">
            <div class="col-sm-8">Активировать/Заморозить сбор данных</div>
            <div class="col-sm-4">
                <VueToggles v-model="item.processingActive" @click="item.processingActive = !item.processingActive" width="130"  height="35" checked-text="Активно" unchecked-text="Выключено"></VueToggles>
            </div>
        </div>

        <div class="form-group row">
            <div class="col-sm-8">Показывать количество действий на лидерборде</div>
            <div class="col-sm-4">
                <VueToggles v-model="item.showActionCount" @click="item.showActionCount = !item.showActionCount" width="70" height="35" checked-text="Да" unchecked-text="Нет"></VueToggles>
            </div>
        </div>

        <div class="form-group row">
            <label class="col-sm-8 col-form-label">Необратимо сбросить ручные баллы</label>
            <div class="col-sm-4">
                <button @click="resetManual" class="btn btn-danger btn-block">Сбросить</button>
            </div>
        </div>

        <div class="row save-btn">
            <div class="col-sm-12 text-center">
                <button @click="store" class="btn btn-primary btn-block mb-3" :disabled="$v.item.$anyError">Сохранить</button>
                <router-link to="/cp/boards"><small>Назад</small></router-link>
            </div>
        </div>

    </div>
</template>

<script>
    import boardApi from "@/services/BoardService";
    import ApiCodes from "@/services/ApiCodes";
    import { required, minLength, minValue, integer } from 'vuelidate/lib/validators';
    import BoardConfigField from "@/components/board/BoardConfigField";
    import {DEFAULT_CONFIG} from "@/components/board/config";

    const fields = {
        page: [
            {
                id: 'name',
                title: 'Название страницы'
            },
            {
                id: 'postDepth',
                title: 'Количество последних постов для рейтинга'
            }
        ],
        actions: [
            {
                id: 'likePoints',
                title: 'Баллы за лайки к постам страницы'
            },
            {
                id: 'commentPoints',
                title: 'Баллы за комментарии к постам страницы'
            },
            {
                id: 'commentMinLength',
                title: 'Минимальная длина комментария'
            },
            {
                id: 'maxCommentsFromUser',
                title: 'Максимум комментариев от одного пользователя'
            },
            {
                id: 'commentReplyPoints',
                title: 'Баллы за ответы на чужие комментарии'
            },
            {
                id: 'commentReplyMinLength',
                title: 'Минимальная длина ответа на чужой комментарий'
            },
            {
                id: 'maxCommentRepliesFromUser',
                title: 'Максимум ответов на чужие комментарии'
            },
            {
                id: 'commentLikePoints',
                title: 'Баллы за лайки чужим комментариям'
            }
        ],
        feedback: [
            {
                id: 'deepReplySubscriberPoints',
                title: 'Баллы за ответы на комментарии от подписчиков'
            },
            {
                id: 'deepReplyNonSubscriberPoints',
                title: 'Баллы за ответы на комментарии не от подписчиков'
            },
            {
                id: 'maxDeepRepliesFromUser',
                title: 'Максимум ответов на комментарий от одного пользователя'
            },
            {
                id: 'deepLikeSubscriberPoints',
                title: 'Баллы за лайки на комментарии от подписчиков'
            },
            {
                id: 'deepLikeNonSubscriberPoints',
                title: 'Баллы за лайки на комментарии не от подписчиков'
            }

        ]
    };

    const validations = [...fields.page, ...fields.actions, ...fields.feedback]
        .map(({id}) => id).reduce((r, id) => ({...r, [id]: {required, integer, minValue: minValue(0)}}), {});

    validations.name = {
        required,
        minLength: minLength(5)
    };

    export default {
        name: "BoardConfig",
        components: {BoardConfigField},
        mixins: [],
        data() {
          return {
              loading: false,
              error: null,
              item: {},
              fields,
              isNew: false
          }
        },
        validations : {
            item: validations
        },
        created() {
            this.loading = true;
            this.error = null;
            if(this.$route.params.id === 'new') {
                this.isNew = true;
                this.loading = false;
                this.item = DEFAULT_CONFIG;
            } else {
                const id = parseInt(this.$route.params.id);
                this.handleResponse(() => boardApi.getBoardConfig(id))
                    .then(data => {
                        if(data){
                            this.item = data;
                        }
                    });
            }
        },
        methods: {
            store() {
                if(!this.loading && !this.$v.item.$invalid) {
                    this.handleResponse(() => boardApi.storeBoard(this.item))
                    .then(data => {
                        if(data) {
                            this.$router.push('/cp/boards');
                        }
                    });
                }
            },
            resetManual() {
                if(this.item.id && !this.loading) {
                    this.handleResponse(() => boardApi.resetManualPoints(this.item.id));
                }
            },
            handleResponse(request) {
                this.error = null;
                this.loading = true;
                return request()
                    .catch(err => {
                        switch (err.message) {
                            case ApiCodes.TOKEN_INVALID:
                                this.$router.push('/login');
                                break;
                            case ApiCodes.ENTITY_NOT_FOUND:
                                this.$router.push('/cp/boards');
                                break;
                            default:
                                this.error = "Произошла ошибка. Повторите запрос позже";
                        }
                        return null;
                    }).finally(() => {
                        this.loading = false;
                    })
            }
        }
    }
</script>

<style scoped>
    .content {
        max-width: 800px;
        width: 100%;
        background-color: white;
        padding: 30px 50px 0;
        align-self: center;
        justify-self: flex-start;
        margin-top: 10px;
        margin-bottom: 10px;
    }
    h5 {
        margin-top: 30px;
    }
    .save-btn {
        margin: 50px -15px 20px;
    }
</style>