<template>
    <nav>
        <ul class="pagination pagination-sm" v-if="options.length > 1">
            <li class="page-item" v-if="curPage!==0">
                <a class="page-link" href="javascript:void(0)" aria-label="Previous" @click="handler(curPage-1)">
                    <span aria-hidden="true">&laquo;</span>
                </a>
            </li>
            <li class="page-item" :class="{active: option===curPage}" v-for="option of options" :key="option">
                <a class="page-link" href="javascript:void(0)" @click="handler(option)" v-if="option!==curPage">{{option + 1}}</a>
                <span class="page-link" v-if="option===curPage">{{option + 1}}</span>
            </li>
            <li class="page-item" v-if="curPage!==numPages-1">
                <a class="page-link" href="javascript:void(0)" aria-label="Next" @click="handler(curPage+1)">
                    <span aria-hidden="true">&raquo;</span>
                </a>
            </li>
        </ul>
        <p>Всего: {{total}}</p>
    </nav>
</template>

<script>

    const START_SPAN = 2;
    const SPAN = 2;
    const END_SPAN = 1;

    function calcOptions(n, cur) {
        const options = [];
        if(n > 1) {
            if(cur > SPAN) {
                for (let i = 0; i < START_SPAN && i < n; i++) {
                    options.push(i);
                }
            }
            for(let i = Math.max(cur - SPAN, 0); i <= Math.min(cur + SPAN, n - 1); i++) {
                if(!options.includes(i)){
                    options.push(i);
                }
            }
            if(cur < n - SPAN) {
                for(let i = n - END_SPAN; i < n; i ++) {
                    if(!options.includes(i)){
                        options.push(i);
                    }
                }
            }
        }

        return options;
    }




    export default {
        name: "Pagination",
        props: ['numPages', 'curPage', 'handler', 'total'],
        data() {
            return {
                options: calcOptions(this.numPages, this.curPage)
            }
        },
        watch: {
            numPages(n, o) {
                this.options = calcOptions(n, this.curPage);
            },
            curPage(n,o) {
                this.options = calcOptions(this.numPages, n);
            }
        },

    }
</script>

<style scoped>
    nav {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-top: 30px;
    }
    nav p {
        margin-left: 50px;
    }
</style>