import Login from "@/components/login/LoginView";
import BoardsView from "@/components/board/BoardsView";
import BoardView from "@/components/board/BoardView";
import PublicBoardView from "@/components/board/PublicBoardView";
import AdminUsersView from "@/components/admin/AdminUsersView";
import AdminBoardsView from "@/components/admin/AdminBoardsView";
import BoardConfig from "@/components/board/BoardConfig";


const routes = [
    {path: '/login', component: Login, name: 'login', alias:'/'},
    {path: '/cp/boards', component: BoardsView, name: 'home'},
    {path: '/cp/boards/:id', component: BoardView},
    {path: '/cp/boards/:id/config', component: BoardConfig},
    {path: '/admin/users', component: AdminUsersView},
    {path: '/admin/users/:id/boards', component: AdminBoardsView},
    {path: '/:code', component: PublicBoardView, name: 'public-board'}
];

export default routes;