<template>
    <div class="root" :style="{backgroundColor: style.background || '#C4D3DC'}">
        <div class="banner" :style="{backgroundImage: style.banner && `url(${style.banner})`}">
            <p v-if="style.title" class="title">{{style.title}}</p>
        </div>
        <div class="content">
            <div class="form-row">
                <div class="col-md-4">
                    <input type="text" class="form-control mb-2" placeholder="Найти аккаунт" name="query" @keyup.enter="filter">
                </div>
            </div>
            <p v-if="loading" class="text-center m-2">Загрузка...</p>
            <div class="table-responsive mt-4">
                <table class="table items-tbl">
                    <thead>
                    <tr>
                        <th>#</th>
                        <th>аккаунт</th>
                        <th>коментарии</th>
                        <th>лайки</th>
                        <th>прочие действия</th>
                        <th>баллы</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr :key="item.pos" v-for="item of list">
                        <th>{{item.pos}}</th>
                        <td>{{item.login}}</td>
                        <td>{{item.commentPoints}}</td>
                        <td>{{item.likePoints}}</td>
                        <td>{{item.otherPoints}}</td>
                        <td>{{item.total}}</td>
                    </tr>
                    </tbody>
                </table>
            </div>
            <Pagination :total="total" :num-pages="numPages" :cur-page="curPage" :handler="loadPage"></Pagination>
        </div>
    </div>
</template>

<script>
    import Pagination from "@/components/util/Pagination";
    import {HandleApiResponse} from "@/components/util/mixins";
    import boardApi from "@/services/BoardService";

    export default {
        name: "PublicBoardView",
        components: {Pagination},
        mixins: [HandleApiResponse],
        data() {
            return {
                style: {},
                list: [],
                loading: true,
                error: null,
                code: null,
                total: 0,
                numPages: 0,
                curPage: 0,
                query: null
            }
        },
        created() {
            this.code = this.$route.params.code;
            this.handleResponse(() => boardApi.getPublicStyle(this.code))
                .then(data => {
                    if(data) {
                        this.style = data;
                    }
                });
            this.loadPage(0);
        },
        methods: {
            filter() {
                this.loadPage(0);
            },
            loadPage(page) {
                this.handleResponse(() => boardApi.getPublicStat(this.code, this.query, page))
                    .then(data => {
                        if(data) {
                            this.list = data.users;
                            this.curPage = data.page;
                            this.numPages = Math.ceil(data.total / data.size);
                            this.total = data.total;
                        }
                    });
                return false;
            }
        }
    }
</script>

<style scoped>
    .root {
        display: flex;
        flex-direction: column;
    }
    .title {
        white-space: pre;
        font-size: 9vmin;
        color: white;
        text-transform: uppercase;
        font-weight: bold;
        line-height: 1.2;
        max-width: 100%;
    }
    .banner {
        overflow: hidden;
        width: 100%;
        height: 25vh;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .content {
        width: 100%;
        margin: 0 auto 20px;
        max-width: 1200px;
        padding: 30px 50px 0;
        align-self: center;
        flex-grow: 1;
        background: #FAFAFA;
        box-shadow: 0 2px 4px 2px rgba(0, 0, 0, 0.25);
    }
    .items-tbl tbody td:nth-child(n+3) {
        text-align: center;
    }
    .items-tbl tbody td:nth-child(2) {
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: 200px;
    }
    .items-tbl thead th {
        text-align: center;
    }
</style>