
import mock from "@/services/mock"; 

async function delay() {
    return mock.delay(300);
}

async function getBoards(page) {
    await delay();
    return {...mock.boards, page};
}

async function getBoardConfig(id) {
    await delay();
    return {...mock.boardConfig, id};
}

async function getBoardStats(id, page = 0, query = '') {
    await delay();
    return {...mock.boardStats, id, page}
}

async function resetManualPoints(id) {
    console.log(`Reset manual points for ${id}`);
    await delay();
    return true;
}

async function deleteBoard(id) {
    console.log(`Delete board ${id}`);
    await delay();
    return true;
}

async function getPaymentLink(id) {
    console.log(`Get payment link`);
    await delay();
    return { link: `${mock.BASE}payment.html?id=${id}`};
}

async function changeBonus(id, account, delta) {
    console.log(`Changing bonus points on board ${id} for ${account} by ${delta}`);
    await delay();
    return true;
}

async function storeBoard(item) {
    console.log('Stored board', item)
    await delay();
    return {...item, id: item.id || 1};
}

async function storeBoardStyle(id, style) {
    console.log('Stored board style', style);
    await delay();
    return {...mock.boardStyle, ...style};
}

async function getBoardStyle(id) {
    await delay();
    return {...mock.boardStyle, id};
}

async function getPublicStyle(code) {
    await delay();
    return mock.boardStyle;
}

async function getPublicStat(code, query = '', page = 0) {
    await delay();
    return {...mock.boardStats, page};
}


export default {getBoards, getBoardConfig, getBoardStats, storeBoard,
    resetManualPoints, deleteBoard, getPaymentLink, storeBoardStyle, getBoardStyle,
    getPublicStat, getPublicStyle, changeBonus};